<template>
  <Container
    ititle="招聘需求发布"
    iwidth="32"
    iheight="35"
  >
    <table id="scroll-title">
      <tr>
        <th width="40%" align="center">招收单位</th>
        <th width="60%" align="center">招收博士后项目</th>
      </tr>
    </table>

    <div id="scroll-content">
      <div id="scroll-table">
        <table width='100%'>
          <tr v-for="(item, index) in infos" :key="index" style="width='100%'">
            <td width="40%" align="center">
              <div>
                <div v-html="item.name">
                  {{ item.name }}
                </div>
                <aside class="contact">
                  {{ item.contact }}
                  <br />
                  {{ item.phone }}
                  <br />
                  {{ item.email }}
                </aside>
              </div>
            </td>

            <td width="60%" align="center">
              {{ item.project_name }}
            </td>
          </tr>
        </table>

        <table></table>
      </div>
    </div>
  </Container>
</template>

<script>
import Container from "./container.vue";

export default {
  name: "recruitmentBoard",
  components: {
    Container,
  },
  data(){
      return{
          infos:''
      }
  },
  mounted() {
    this.infos = require('../assets/infos.json').data
    let c1 = document.getElementById("scroll-table");
    let ca = document.getElementById("scroll-content");
    setInterval(function st() {
      if (ca.scrollTop >= c1.offsetHeight / 2) {
        ca.scrollTop = 0;
      } else {
        ca.scrollTop++;
      }
      // ca.onmouseover = function() {
      //   clearInterval(cas);
      // };
      // ca.onmouseout = function() {
      //   cas = setInterval("st()", 50);
      // };
    }, 100);
  },
};
</script>

<style scoped>
@import "../styles/recruitmentBoard.css";
</style>
