<template>
  <div>
    <div id="map" class="map"></div>
    <a id="back-home" class="back-button" v-if="backBtn" @click="back">
      返回
    </a>
  </div>
</template>

<script>

import axios from "axios";
//世界数据
import compJson from "../assets/comp.json"
//中国数据
import chinaJson from "../assets/china.json"
// import convertMapData from "../utils/convertMapData"


export default {
  name: "index",
  data() {
    return {
      map: {},
      backBtn: false,
      mapHistory:[],
      length: 0,
      maxLength: 1,
      //要展示的数据
      PointJson:compJson.array,
    };
  },
  created(){
    this.getWorldJson()
    this.mapHistory.push()
  },
  mounted(){
    console.log(document.querySelector('#map').className)
  },
  watch:{
    //普通监听，还有深度监听
    length(val,oldVal){
      if(val == 0){
        this.backBtn = false
      }
      if(val == 1){

      }
    }
  },
  methods: {
    async getWorldJson(){
      //axios获取本地的Json是放在public的
      //获得世界地图数据
      let {data:geoJson} = await axios.get('./countries2.geo.json')
      // let {data:mapTopJson} = await axios.get('./mapTop.json')
      this.worldMap(geoJson)
    },
    worldMap(geoJson){

      let compJson = require('./../assets/comp.json')
      var compJsonDisplay = compJson.display

      this.$echarts.registerMap("world", geoJson);
      let myChart = this.$echarts.init(document.getElementById("map"));
      let setOption = {
          title: {
            subtext: "",
            left: "center",
            top: "3%",
            textStyle: {          
              color: "#d5e7f3",
              fontSize: 18,
              fontFamily:'微软雅黑',
              fontWeight:'bold'
            },
          },
          tooltip: {
            trigger:"item",
            formatter: function(params) {
                return params.name + ":" +params.data.value[0]
            },
          },
          geo: {
            map: "world",
            show: true,
            //是否开启鼠标缩放和平移漫游
            roam: false,
            left: "center",
            label:{
              show: true,
              color: "#404040",
              // color:"#fff",
              fontSize:'oblique',
              fontWeight: 'bolder',
              formatter(params){
                //这个label展示的是地图上显示的名字
                if(compJsonDisplay[params.name]){
                   return params.name
                }
                else{
                    return ''
                }
                   
              }
            },
            
            // 设置放大比例
            zoom:1.2,
          },
          series: [
            //第一个数据（地图）
            {
              type: "map",
              map: "world",
              //和geo绑定
              geoIndex: 0,
              data: this.PointJson
            }
          ],
          visualMap:{
            min: 0,
            max: 1200,
            calculable: true,  
            color: ["red","darkorange", "gold", "lawngreen", "lightseagreen","white"] 
          }
      }


      myChart.setOption(setOption,true);
      window.addEventListener("resize", function() {
        myChart.resize();
      });


      if(this.length==0){
        myChart.resize();
      }

      myChart.on('click',async(data)=>{
        console.log(data.name)
        let name
        name = data.name
        let {data:compJson} = await axios.get('./comp.json')
        // if(this.length == 0){
            for(let i = 0;i<compJson.array.length;i++){
            //如果进行下钻，还需要下一级
            if(name == compJson.array[i].name){
              //保存Json地址
              var newI = compJson.array[i].json
              //保存最终的json地址
              var newAddr = `./countries/`+newI+`.geo.json`
              //导入相对应的json
              let {data:newJson} = await axios.get(newAddr)
              
              //进入下一级前先检查是否已经到底了，如果没到底就Push,并且length+1
              if(this.length<this.maxLength){
                this.mapHistory.push(name)
                this.length+=1
              }
              this.$echarts.registerMap("world", newJson);
              setOption.series[0].data = chinaJson.world
              myChart.setOption(setOption);
              //成功找到后始终保持返回按钮显示
              this.backBtn = true 
              this.mapClass = false
              document.querySelector('.map').style.width = '60%'
              myChart.resize();
              this.$router.push({path:'/index'})
              document.querySelector('body').style.backgroundColor = '#000c3b'
            }
          }
      })


    },
    async back(){
      
      //判断长度
      if(this.length>0){
        //先踢出去一个
        this.mapHistory.pop()
        this.length --  
      }
      
      if(this.mapHistory.length < 1){
        this.getWorldJson()
        document.querySelector('.map').style.width = '100%'
        this.$router.push({path:'/'})
        document.querySelector('body').style.backgroundColor = '#99bbff'
      }
      else{
        let historyCityName = this.mapHistory[this.mapHistory.length -1]
        let { data:MapJson } = await axios.get(`./countries/`+historyCityName+`.geo.json`)
        this.worldMap(MapJson)
      }
    },
  },
};
</script>

<style scoped>
@import "../styles/map.css";
</style>
